import { Formik } from 'formik';
import * as yup from 'yup';
import { Button, Form } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { authOperations } from 'redux/auth/authOperations';
import {
  ErrorMessageBox,
  FormContainer,
  InputContainer,
  InputStyled,
} from 'components/LogIn/LogIn.styled';

const Registration = () => {
  const dispatch = useDispatch();

  const handleSubmitForm = ({ name, email, password }) => {
    dispatch(authOperations.register({ name, email, password }));
  };

  //email: "shenyalipinskiy@gmail.com"
  //name: 'Eugene';
  //password: '123456789';

  const nameRegExp =
    /^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$/;

  const validationSchema = yup.object().shape({
    name: yup
      .string()
      .matches(
        nameRegExp,
        `*'   , ,   `
      )
      .min(2, `*'     `)
      .required(`*' '`),
    email: yup
      .string()
      .email('*    ')
      .required(`* '`),
    password: yup
      .string()
      .min(6, '*    6 ')
      .required(`* '`),
  });

  return (
    <Formik
      initialValues={{
        name: '',
        email: '',
        password: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting, resetForm }) => {
        handleSubmitForm(values);
        setSubmitting(true);
        resetForm();
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        isSubmitting,
      }) => {
        return (
          <FormContainer>
            <Form name="registration" noValidate onSubmit={handleSubmit}>
              <InputContainer className="mb-5" controlId="formBasicName">
                <Form.Label>':</Form.Label>
                <InputStyled
                  type="name"
                  name="name"
                  placeholder=" '"
                  value={values.name}
                  onChange={handleChange}
                  className={touched.name && errors.name ? 'error' : null}
                  required
                />
                {touched.name && errors.name ? (
                  <ErrorMessageBox className="error-message">
                    {errors.name}
                  </ErrorMessageBox>
                ) : null}
              </InputContainer>

              <InputContainer className="mb-5" controlId="formBasicEmail">
                <Form.Label>:</Form.Label>
                <InputStyled
                  type="email"
                  name="email"
                  placeholder=" "
                  value={values.email}
                  onChange={handleChange}
                  className={touched.email && errors.email ? 'error' : null}
                  required
                />
                {touched.email && errors.email ? (
                  <ErrorMessageBox className="error-message">
                    {errors.email}
                  </ErrorMessageBox>
                ) : null}
              </InputContainer>

              <InputContainer className="mb-5" controlId="formBasicPassword">
                <Form.Label>:</Form.Label>
                <InputStyled
                  type="password"
                  name="password"
                  placeholder=" "
                  value={values.password}
                  onChange={handleChange}
                  className={
                    touched.password && errors.password ? 'error' : null
                  }
                  required
                />
                {touched.password && errors.password ? (
                  <ErrorMessageBox className="error-message">
                    {errors.password}
                  </ErrorMessageBox>
                ) : null}
              </InputContainer>

              <Button variant="primary" type="submit" disabled={isSubmitting}>
                
              </Button>
            </Form>
          </FormContainer>
        );
      }}
    </Formik>
  );
};

export default Registration;
